	export default {
		data() {
			return {
				title: 'createSelectorQuery',
				top: 0,
				left: '0px',
				info: []
			}
		},
		onReady() {
			this.getNodeInfo()
		},
		methods: {
			setPosition() {
				this.left = Math.random() * uni.upx2px(320) + 'px'
				this.top = Math.random() * uni.upx2px(320) + 'px'
				this.getNodeInfo()
			},
			getNodeInfo() {
				uni.createSelectorQuery().select('.target').boundingClientRect().exec((ret) => {
					const rect = ret[0]
					if (rect) {
                        const sort = ['left','right','top','bottom','width','height']
						const info = []
						for (let i in sort) {
                            let key = sort[i]
                            info.push({
                                'key': key,
                                'val': rect[key]
                            })
						}
						this.info = info
					}
				});
			}
		},
	}
